.bouton-nav {
    cursor: pointer;
    margin: 10px;
    width: 45px;
    height: auto;
    transition: 300ms;
    position: fixed;
    z-index: 5;
}

.bouton-nav:hover {
    cursor: pointer;
    margin: 10px;
    width: 45px;
    height: auto;
    transition: 300ms;
    position: fixed;
    z-index: 5;
    transform: scale(1.06);
}


.bouton-nav-active {
    cursor: pointer;
    margin: 10px;
    width: 45px;
    height: auto;
    transition: 300ms;
    position: fixed;
    z-index: 5;
    transform: scale(1.06);
}