.section {
    display: grid;
    grid-template-columns: 33% 23% 2% 39% 1fr;
}

.separator-profil {
    grid-column: 3;
    align-items: center;
    width: 7px;
    height: 60%;
    background-color: white;
    border-radius: 15px;
   justify-self: center;
   align-self: center;
}

.section .contact {
    font-family: 'Quicksand', sans-serif;
    font-size: 1.9em;
    line-height: 2;
    justify-self: center;
   align-self: center;
   text-align: center;
    grid-column: 2;
}

.section .text {
    font-family: 'Quicksand', sans-serif;
    font-size: 1.2em;
    line-height: 1.6;
    text-align: center;
    justify-self: center;
   align-self: center;
    grid-column: 4;
    margin-left: 10px;
    margin-right: 10px;
}

.profil {
   grid-column: 1;
   align-self: start;
   max-width: auto;
    height: 100vh;
}

@import url('https://fonts.googleapis.com/css?family=Quicksand&display=swap');